import PageButton from '../../components/button.vue';

export default {
	components: {
		PageButton,
	},

	data() {
		return {
			temp:{
				inquiryId:'',
				detail:'',
				picUrl:'',
				realName:'',
				idCard:"",
				gender:"",
				weight:'',
				allergic:'',
				conceive:'',
				liver:'',
				kidney:'',
				medicalHistory:'',
				states:'',
				doctorId:'',
				doctorName:'',
				introduction:'',
				receptId:'',
				de:''
			},
			picUrls:[]
		}
	},
	onLoad(options) {
		this.temp.inquiryId = options.inquiryId;
		this.getInquiryDetail();
	},
	onShow() {
		
	},
	methods: {
		getInquiryDetail:function(){
			let that = this;
			var dd = {"inquiryId":that.temp.inquiryId}
			that.$util.request({
				url: "/didi-patient/inquiryinfo/getDetailInfo" ,
				param: dd,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					that.temp = res.data;
					that.picUrls = that.temp.picUrl.split(',');
				},
				error: function() {}
			})
		},
		commentDoctor:function(){
			uni.navigateTo({
				url:'/pages/my/commentDoctor?detail='+JSON.stringify(this.temp)
			})
		},
		cancelInquiry:function(){
			let that = this;
			var dd = {"inquiryId":that.temp.inquiryId}
			that.$util.request({
				url: "/didi-patient/inquiryinfo/cancelInquiry" ,
				param: dd,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.showToast({
						title: '取消成功',
						icon: 'success',
						duration: 2000
					})
					that.getInquiryDetail();
				},
				error: function() {}
			})
		}
		
	}
}
